:root {
  --flex-item: 5;
  --flex-item-gap: 20px;
  --main-text-color: #111;
  --main-text-color-60: rgba(17, 17, 17, .6);
  --extra-text-color: #4f2ee8;
  --sub-text-color: #b4afaf;
  --light-text-color: #fff;
  --light-text-color-60: rgba(255, 255, 255, .6);
  --place-text-color: #eac645;
  --dark-text-color-60: rgba(0, 0, 0, .6);
  --main-bg-color: #111;
  --extra-bg-color: #4f2ee8;
  --place-bg-color: #eac645;
  --bg-color: #f6f6f6;
  --extra-border-color: #4f2ee8;
  --extra-border-color-40: rgba(79, 46, 232, .4);
  --empty: #fff;
  --transition-property: color, background, box-shadow, transform;
  --transition-duration: .3s;
  --transition-timing-function: ease-out;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background: #e0e0e0;
  border-radius: 12px;
  transition: all .3s ease-in-out;
}

::-webkit-scrollbar-thumb:hover {
  background: #b4b4b4;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  color: var(--main-text-color);
  letter-spacing: -.04em;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28571;
}

body.light-theme {
  background: #f6f6f6;
}

body.dark-theme {
  background: #202024;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 24px;
  }
}

.global-title, .global-p {
  margin: 0;
}

.global-link {
  color: currentColor;
  text-decoration: none;
}

.global-button {
  color: currentColor;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  letter-spacing: inherit;
  cursor: pointer;
  border: none;
}

.global-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.flex-container {
  gap: var(--flex-item-gap);
  flex-wrap: wrap;
  display: flex;
}

.main-block > .container {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .main-block > .container {
    gap: 100px;
  }
}

@media screen and (min-width: 1440px) {
  .main-block > .container {
    flex-direction: row;
    gap: 40px;
  }
}

.asides {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .asides {
    flex-direction: row;
    gap: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .asides {
    flex-direction: column;
    flex: auto;
    gap: 86px;
  }
}

header {
  width: 100%;
  height: 64px;
  background: var(--light-text-color);
  border: 1.5px solid var(--main-text-color);
  border-radius: 0 0 8px 8px;
  margin-bottom: 40px;
  position: absolute;
  top: 0;
  left: 0;
}

.logo {
  cursor: pointer;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 16px;
  transform: translateY(-50%);
}

.text-logo {
  cursor: pointer;
  color: var(--main-text-color);
  align-items: center;
  margin-left: 8px;
  font-weight: bold;
  text-decoration: none;
  display: flex;
}

.text-logo img {
  margin-right: 8px;
}

.logo-list {
  list-style-type: none;
}

.header-links {
  align-items: center;
  list-style-type: none;
  display: flex;
  position: absolute;
  top: 50%;
  left: 200px;
  transform: translateY(-50%);
}

.header-link {
  margin-left: 16px;
  list-style-type: none;
}

.link-text {
  color: var(--main-text-color);
  align-items: center;
  text-decoration: none;
  display: none;
}

.link-text.shopping-link:before {
  content: "";
  width: 20px;
  height: 20px;
  background-image: url("icons.351431df.svg#icon-shop");
  background-repeat: no-repeat;
  background-size: 20px;
  margin-right: 8px;
  display: inline-block;
}

.switcher {
  width: 40px;
  height: 24px;
  background: linear-gradient(180deg, var(--extra-bg-color) 0%, var(--light-text-color-60) 100%);
  cursor: pointer;
  border-radius: 12px;
  margin-right: 40px;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.switcher.active {
  background: linear-gradient(180deg, #4f2ee8 0%, var(--sub-text-color) 100%);
}

.switcher-handle {
  width: 20px;
  height: 20px;
  background: -20px 0 no-repeat;
  background-color: var(--light-text-color);
  border-radius: 50%;
  transition: left .3s ease-in-out;
  position: absolute;
  top: 2px;
  left: 1px;
}

.switcher-circle {
  width: 18px;
  height: 18px;
  fill: var(--light-text-color);
  position: absolute;
  top: 3px;
  left: 1px;
}

.switcher.active .switcher-handle {
  background-position: 0 0;
  left: calc(100% - 22px);
}

.user-profile {
  display: none;
}

.avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.username {
  margin-left: 10px;
}

.menu-button {
  width: 28px;
  height: 28px;
  cursor: pointer;
  background: none;
  margin-left: 16px;
  padding: 0;
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.menu-icon {
  width: 100%;
  height: 100%;
  fill: var(--main-text-color);
}

.modal {
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  width: 80%;
  max-width: 400px;
  background-color: var(--light-text-color);
  text-align: center;
  border-radius: 8px;
  margin: 20% auto;
  padding: 20px;
  position: relative;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
}

.modal-close {
  color: var(--main-text-color);
  cursor: pointer;
  font-size: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
}

@media only screen and (max-width: 400px) {
  .desktop-menu {
    display: none;
  }

  .menu-button {
    display: block;
  }

  .header-links {
    display: none;
  }
}

@media only screen and (min-width: 750px) {
  .desktop-menu, .menu-button {
    display: none;
  }

  .header-links {
    align-items: center;
    display: flex;
    position: absolute;
    top: 30%;
    left: 120px;
    transform: translateY(-50%);
  }

  .link-text {
    display: flex;
  }

  .header-link {
    color: var(--main-text-color);
    margin-left: 24px;
    text-decoration: none;
  }

  .home-link a, .shopping-link a {
    width: 100%;
    height: 34px;
    background: var(--place-bg-color);
    border-radius: 36px;
    padding: 0 10px;
    font-weight: bold;
  }

  .switcher {
    right: 96px;
  }

  .user-profile {
    align-items: center;
    padding-right: 20px;
    display: flex;
    position: absolute;
    top: 14px;
    right: 1px;
  }
}

.support-aside {
  width: 335px;
  height: 352px;
  background: radial-gradient( 183.6% 183.01% at 59.76% -23.42%, var(--extra-text-color) 18.03%, var(--light-text-color) 100% );
  border-radius: 10px;
  position: relative;
  box-shadow: 0 10px 10px rgba(245, 245, 247, .1);
}

.support-aside, .support-item a {
  color: var(--light-text-color);
}

.support-list {
  height: 200px;
  scroll-behavior: smooth;
  padding-left: 0;
  overflow-y: scroll;
}

.support-list::-webkit-scrollbar {
  display: none;
}

.support-img-01 {
  width: 131px;
}

.support-img-02 {
  width: 62px;
}

.support-img-03 {
  width: 101px;
}

.support-img-04 {
  width: 82px;
}

.support-img-05 {
  width: 55px;
}

.support-img-06 {
  width: 116px;
}

.support-img-07 {
  width: 149px;
}

.support-img-08 {
  width: 133px;
}

.support-img-09 {
  width: 81px;
}

.support-title {
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  padding-top: 24px;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.14;
  display: flex;
}

.support-title img {
  margin-left: 12px;
}

.support-svg {
  height: 5.98px;
  width: 11.67px;
  fill: var(--extra-text-color);
  display: block;
}

.support-button {
  background: var(--light-text-color);
  border-radius: 50%;
  padding: 13.1px 10.17px 12.93px;
  transition: transform .3s;
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.support-button.support-button-rotate {
  transform: translate(-50%, -50%)scale(1)rotate(180deg);
}

.support-item {
  align-items: center;
  gap: 16px;
  margin-bottom: 15px;
  margin-left: 40px;
  display: flex;
}

.item-number {
  line-height: 1.29;
}

@media screen and (min-width: 376px) and (max-width: 767px) {
  .support-aside {
    height: 472px;
  }

  .support-button {
    bottom: 0;
  }
}

@media screen and (min-width: 768px) {
  .support-aside {
    width: 356px;
    height: 490px;
  }
}

@media screen and (min-width: 376px) {
  .support-list {
    height: 300px;
  }

  .support-svg {
    height: 6.58px;
    width: 12.83px;
  }

  .support-button {
    padding: 15.8px 12.58px 15.62px;
  }
}







